
import { useEffect, useState,useRef } from "react";
import { useGLTF,Html } from '@react-three/drei'

 export default function Ball({...props}) {
  // This reference will give us direct access to the mesh
  const mesh = useRef()
  // Set up state for the hovered and active state
  const [hovered, setHover] = useState(false)
  const [active, setActive] = useState(false)
  const {category} = props 
  //console.log("category",category);
  // Subscribe this component to the render-loop, rotate the mesh every frame
  //useFrame((state, delta) => (mesh.current.rotation.x += 0.01))
  // Return view, these are regular three.js elements expressed in JSX
  const pointClick = (event) =>{
    if (typeof window.triggerCategoryClick === "function") { 
        // safe to use the function
        window.triggerCategoryClick(category.id,!active);
    }
    setActive(!active)
  }
  return (

    <group >
        <mesh
          {...props}
          ref={mesh}
          scale={active ? 1.5 : 1}
          onClick={pointClick}
          onPointerOver={(event) => setHover(true)}
          onPointerOut={(event) => setHover(false)}>
          
          <sphereBufferGeometry args={[.05, 64, 64]} />
          <meshStandardMaterial color={hovered ? 'orange' : 'blue'} />
          {active|hovered && category && <Html scale={0.3}  position={[ 0.5, 0, 0]} transform occlude>
                  <div className="annotation">
                    <h2 style={{margin:"0px",fontSize: '1.6em' }}>{category.name}</h2>                
                    {active && <a href={category.link} target="_blank" rel="noopener noreferrer">Link</a>}
                  </div>
                </Html> }
        </mesh>
    </group>
  )
}